.rk-logo{
    display: flex;
    padding-top: 30px;
    justify-content: center;
}

.rk-header-logo{
    display: inline-flex;
    text-decoration: none!important;
    .colored_theme_svg{
        fill: #365edc;
    }
}


.rk-header-bar{
    display: flex;
    align-items: center;
    justify-content: center;
}

.rk-header-anim{
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    font-size: 14px;
    font-weight: 600;
    position: relative;
    width: 150px;
    transition: width ease 0.4s;
    transition-delay: 0.5s;
    &.anim{
        width: 100%;
    }
    &_line{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 1;
        display: flex;
        align-items: center;
        svg{
            height: 14px;
        }
    }
    &_t1{
        width: 60px;
        background-color: #fff;
        position: relative;
        z-index: 2;
        padding-left: 10px;
    }
    &_t2{
        width: 95px;
        background-color: #fff;
        position: relative;
        z-index: 2;
        padding-right: 10px;
    }
}

.rk-header-menu{
    &_toggle{
        display: inline-flex;
        margin-left: auto;
        margin-right: auto;
        align-items: center;
        padding: 10px;
        margin-bottom: 20px;
        color: #000;
        font-weight: 600;
        font-size: 14px;
        &:hover,
        &:focus{
            text-decoration: none;
            color: #000;
        }
        .chevron{
            width: 15px;
        }
    }
}

.rk-header-list{
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    &__item{
        width: 20%;
        > a{
            color: #000!important;
            font-size: 18px;
            display: block;
            text-decoration: none;
            margin-bottom: 15px;
        }
    }
}